/* App styles */
.App {
	text-align: center;
}

/* element styles */
.header {
	font-family: "Raleway";
	font-size: 40px;
	font-weight: bold;
	line-height: 24px;
	padding-top: 100px;
	text-align: center;
	color: #444649;
}

.page {
	position: relative;
	top: 0;
	left: 0;
}

.divider {
	border: 2px solid #3f4e67;
	width: 100px;
	margin: 20px 0 0 0;
}

.divider.pink {
	border-color: #df777b;
	background-color: #df777b;
}

.divider.yellow {
	border-color: #fbe77d;
	background-color: #fbe77d;
}

/* utility styles */
.white {
	color: white;
}

.center {
	text-align: center;
	margin-left: auto;
	margin-right: auto;
}
/* 
.hidden {
	display: none;
} */

.disabled {
	pointer-events: none;
}

